import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Brochure, BrochureCategory, BrochurePage } from 'src/app/objects/brochure.model';
import { Message,MenuVisitor } from 'src/app/objects/message.model';
import { MessageService } from 'src/app/services/message.service'; 
import { IonContent, Platform } from '@ionic/angular';
import { Location } from '@angular/common';
import { PopoverController } from '@ionic/angular';
import { BrochurePopoverCatListPage } from 'src/app/viewer/brochure-popover-cat-list/brochure-popover-cat-list.page'
import { ActionSheetController } from '@ionic/angular';
import { Router } from '@angular/router'; 
import { AuthService } from 'src/app/services/auth.service';
import * as firebase from 'firebase';
import { UrlService } from 'src/app/services/url.service';

@Component({
  selector: 'app-brochure',
  templateUrl: './brochure.page.html',
  styleUrls: ['./brochure.page.scss'],
})
export class BrochurePg implements OnInit {
  cid: string;
  mid: string;
  message: Message;
  brochure: Brochure = new Brochure();
  activeCategori:BrochureCategory = new BrochureCategory();
  activePage:BrochurePage = new BrochurePage();
  nextPage:BrochurePage = null;
  prevPage:BrochurePage = null;
  activePageIndex:number= 0;
  @ViewChild(IonContent) ionContent: IonContent;
  backBtnSub: any;
  previousUrl: string = '';

  constructor(public route: ActivatedRoute, 
              private actionSheetController:ActionSheetController,
              public popoverController: PopoverController,
              public location:Location,
              public router: Router,
              public authService:AuthService,
              private platform:Platform,
              private urlService:UrlService,
              private messageService:MessageService) { }

 

  ngOnInit() {  
    this.cid  = this.route.snapshot.paramMap.get('cid')
    this.mid  = this.route.snapshot.paramMap.get('mid')

    this.urlService.previousUrl$.subscribe((previousUrl: string) => {
      this.previousUrl = previousUrl
    });
    this.backBtnSub = this.platform.backButton.subscribe(() => {  
      this.back()
    });

    this.messageService.getMessageById(this.cid,this.mid).ref.get().then((snapshot)=>{
      let menuVisitor:MenuVisitor = new MenuVisitor();
      let uid = 'anonim_visitor';
      if(this.authService.USER){
        uid = this.authService.USER.uid
      }
      menuVisitor.uid = uid;
      menuVisitor.created_at = firebase.firestore.FieldValue.serverTimestamp();;
      snapshot.ref.collection('visitors').add({...menuVisitor})
      this.message = <Message>snapshot.data()
      this.brochure = this.message.brochure;
      this.changeCategory(this.brochure.categories[0])
    })
  }

  async showCategoriList(evt:Event){   
      const popover = await this.popoverController.create({
        component: BrochurePopoverCatListPage,
        componentProps: {
          categories: this.brochure.categories,
        }, 
        translucent: true
      });
      popover.onWillDismiss().then((cDat)=>{
        if(cDat.data){
          if(cDat.data['cat'] == 'back'){
            this.back();
          }else{
            this.changeCategory(<BrochureCategory>cDat.data['cat'])
          }
        }
      })
      return await popover.present(); 
  }

  changeCategory(cat:BrochureCategory){   
    cat = cat?cat:new BrochureCategory();
    this.activeCategori = cat;
    this.activePage = cat.pages.length?cat.pages[0]: new BrochurePage(); 
    if(cat.pages[1]){
      this.nextPage = cat.pages[1];
    }
  }

  changePage(artim:any){  
    if(artim == '-'){ 
      this.activePage = this.prevPage;
    }else{ 
      this.activePage = this.nextPage;
    }

    this.activeCategori.pages.map((page,i)=>{
      if(page == this.activePage){
        if(this.activeCategori.pages[i+1]){
          this.nextPage = this.activeCategori.pages[i+1]
        }else{
          this.nextPage =null
        }

        if(this.activeCategori.pages[i-1]){
          this.prevPage = this.activeCategori.pages[i-1]
        }else{
          this.prevPage =null
        }
      }
    })
     
  }

  async openPageList(){
    let action_menu = [];
    this.activeCategori.pages.forEach((p,i)=>{
      action_menu.push({text:p.title,role:'destructive',handler: () => {
        this.activePage = p;
        this.activeCategori.pages.map((page,i)=>{
          if(page == this.activePage){
            if(this.activeCategori.pages[i+1]){
              this.nextPage = this.activeCategori.pages[i+1]
            }else{
              this.nextPage =null
            }
    
            if(this.activeCategori.pages[i-1]){
              this.prevPage = this.activeCategori.pages[i-1]
            }else{
              this.prevPage =null
            }
          }
        })
      }})
    })
    const actionSheet = await this.actionSheetController.create({
      header: 'Sayfalar',
      cssClass: 'my-custom-class',
      buttons: action_menu
    });
    await actionSheet.present();
  } 

  back(){
    if(this.previousUrl == undefined || this.previousUrl == null){
      if(this.backBtnSub){
        this.backBtnSub.unsubscribe();
      }
      this.router.navigate(['home']);
    }else{
      this.location.back();
    }
  }

  ngOnDestroy(){
    if(this.backBtnSub){
      this.backBtnSub.unsubscribe();
    }
  }

}