import { Component, Input, OnInit } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { BrochureCategory } from 'src/app/objects/brochure.model';
import { Location } from '@angular/common';

@Component({
  selector: 'app-brochure-popover-cat-list',
  templateUrl: './brochure-popover-cat-list.page.html',
  styleUrls: ['./brochure-popover-cat-list.page.scss'],
})
export class BrochurePopoverCatListPage implements OnInit {

  @Input() categories: BrochureCategory[];
  
  constructor(private popoverController:PopoverController, private location:Location) { }

  ngOnInit() { 
    
  }

  dismissPopover(cat:BrochureCategory){
    this.popoverController.dismiss({'cat':cat})
  }

  back(){
    this.popoverController.dismiss({'cat':'back'}) 
  }

}
